import Axios from 'axios'
import { Toast } from 'vant'
// import tokenInteface from '@/utils/tokenInteface'
// import store from '@/store'
let reqNUm = 0
let toastInstance = null
// 创建axios实例
const axios = Axios.create({
  baseURL: process.env.VUE_APP_BASE_API, // url基础地址，解决不同数据源url变化问题
  // withCredentials: true, // 跨域时若要发送cookies需设置该选项
  timeout: 10000 // 超时
})

// 请求拦截
axios.interceptors.request.use(
  config => {
    if (reqNUm === 0) {
      toastInstance = Toast.loading({
        duration: 15000, // 持续展示 toast
        message: '请稍后…'
      })
    }
    reqNUm++
    // do something
    const token = localStorage.getItem('token') || ''
    if (token) {
      // 设置令牌请求头
      config.headers.authorization = 'Bearer ' + token
    }
    return config
  },
  error => {
    // 请求错误预处理
    return Promise.reject(error)
  }
)

// 响应拦截
axios.interceptors.response.use(
  // 通过自定义code判定响应状态，也可以通过HTTP状态码判定
  response => {
    // 仅返回数据部分
    reqNUm--
    if (reqNUm === 0) {
      toastInstance.clear()
    }
    const res = response.data

    // code不为200则判定为一个错误
    if (res.code !== 200) {
      const errMsg = res.msg || 'Error'
      Toast.fail(errMsg)
      return Promise.reject(new Error(errMsg))
    } else {
      return res
    }
  },
  error => {
    reqNUm = 0
    toastInstance.clear()
    return Promise.reject(error)
  }
)

export default axios
