const $nativeBridge = {
  native: {
    getUserId () { // 获取用户user_id
      return Promise.resolve((resolve, reject) => {
        window.setjsBridge((bridge) => {
          bridge.callHandler(
            'getUserId', {}, function (res) {
              const response = typeof res === 'string' ? JSON.parse(res) : res
              const userCode = response.data || ''
              console.log('-------------', userCode)
              resolve(userCode)
            }
          )
        })
      })
    },
    getUserToken () { // 获取用户token
      return Promise.resolve((resolve, reject) => {
        window.setjsBridge((bridge) => {
          bridge.callHandler(
            'getUserToken', {}, function (res) {
              const response = typeof res === 'string' ? JSON.parse(res) : res
              const token = response.data || ''
              console.log('-------------', token)
              resolve(token)
            }
          )
        })
      })
    },
    getUserPhone () { // 获取用户手机号
      return Promise.resolve((resolve, reject) => {
        window.setjsBridge((bridge) => {
          bridge.callHandler(
            'getUserPhone', {}, function (res) {
              const response = typeof res === 'string' ? JSON.parse(res) : res
              const phone = response.data || ''
              console.log('-------------', phone)
              resolve(phone)
            }
          )
        })
      })
    },
    closeView () { // 关闭webview
      window.setjsBridge((bridge) => {
        bridge.callHandler(
          'closeView', {}, function () {}
        )
      })
    }
  },
  h5: {
    feedbackSub (cb = function () {}) { // 反馈页面点击提交按钮
      window.setjsBridge((bridge) => {
        bridge.registerHandler('feedbackSub', function (data, callbackFun) {
          console.log('feedbackSub------')
          cb()
        })
      })
    },
    feedbackUploadImg (cb = function () {}) { // 反馈页面点击图片上传按钮
      window.setjsBridge((bridge) => {
        bridge.registerHandler('feedbackUploadImg', function (data, callbackFun) {
          console.log('feedbackUploadImg------')
          cb()
        })
      })
    }
  }
}

export default {
  install (Vue, options) {
    if (this.installed) return

    this.installed = true

    Object.defineProperties(Vue.prototype, {
      $nativeBridge: {
        get () {
          return $nativeBridge
        }
      }
    })
  }
}
