function moneyFilter (v) {
  v = Number(v)
  return parseFloat(v)
}

function QFormat (v, fmt = 'yyyy-MM-dd') {
  const date = new Date(v)
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  const o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (const k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      const str = o[k] + ''
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? str : ('00' + str).substr(str.length)
      )
    }
  }
  return fmt
}

/**
 * 格式化时间    20180202 →→→→→→→ 2018-02-02
 * @param {*value} value
 */
const dateServer = (value) => {
  value = typeof value === 'string' ? value : value + ''

  return value.replace(/(\d{4})(\d{2})(\d{2})/g, '$1-$2-$3')
}

/**
 * 格式化时间至分秒
 */
const timeHadelSeconds = (value) => {
  if (isNaN(value)) {
    return '00.00.00'
  }
  value = Number(value)
  var value1 = value.length > 10 ? value : value * 1000

  var d = new Date(value1)
  var year =
    d.getFullYear() < 10 ? '0' + d.getFullYear() : '' + d.getFullYear()
  var month =
    d.getMonth() + 1 < 10
      ? '0' + Number(d.getMonth() + 1)
      : '' + d.getMonth() + 1
  var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours()
  var minutes =
    d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes()
  var seconds =
    d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds()

  return (
    year + '.' + month + '.' + day + '  ' + hour + ':' + minutes + ':' + seconds
  )
}
/**
 * 格式化时间至日期
 */
const timeHadelDay = (value) => {
  if (value === 0 || value === '') {
    return ''
  }
  value = Number(value)
  if (isNaN(value)) {
    return ''
  }
  var value1 = value.length > 10 ? value : value * 1000

  var d = new Date(value1)
  var year =
    d.getFullYear() < 10 ? '0' + d.getFullYear() : '' + d.getFullYear()
  var month =
    d.getMonth() + 1 < 10
      ? '0' + Number(d.getMonth() + 1)
      : '' + d.getMonth() + 1
  var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()

  return year + '.' + month + '.' + day
}

/**
 * 格式化商品价格
 */
const toDecimal = (value) => {
  var f = parseFloat(value)
  if (isNaN(f)) {
    return false
  }
  f = Math.round(value * 100) / 100
  var s = f.toString()
  var rs = s.indexOf('.')
  if (rs < 0) {
    rs = s.length
    s += '.'
  }
  while (s.length <= rs + 2) {
    s += '0'
  }
  return s
}

// 四舍五入 格式化数字
// toFix(8440.55,1) => 8440.6
function toFixed (number, fractionDigits) {
  var times = Math.pow(10, fractionDigits)
  var roundNum = Math.round(number * times) / times
  return roundNum.toFixed(fractionDigits)
}
/**
 * @desc   格式化${startTime}距现在的已过时间
 * @param  {Date} startTime
 * @return {String}
 */
function formatPassTime (startTime) {
  const startTimeAfter = Date.parse(new Date(startTime))
  var currentTime = Date.parse(new Date())
  var time = currentTime - startTimeAfter
  var day = parseInt(time / (1000 * 60 * 60 * 24))
  var hour = parseInt(time / (1000 * 60 * 60))
  var min = parseInt(time / (1000 * 60))
  var month = parseInt(day / 30)
  var year = parseInt(month / 12)

  if (year) {
    return year + '年前'
  }
  if (month) {
    return month + '个月前'
  }
  if (day) {
    return day + '天前'
  }
  if (hour) {
    return hour + '小时前'
  }
  if (min) {
    return min + '分钟前'
  }
  return '刚刚'
}

function relationtoAppntFilter (str) {
  var personTaxresident = '本人'
  switch (str) {
    case '00':
      personTaxresident = '本人'
      break
    case '01':
      personTaxresident = '配偶'
      break
    case '02':
      personTaxresident = '父母'
      break
    case '03':
      personTaxresident = '子女'
      break
    default:
      personTaxresident = '本人'
  }
  return personTaxresident
}
export {
  capitalize,
  formatTime,
  formatTimeDay,
  dateServer,
  timeHadelSeconds,
  timeHadelDay,
  toDecimal,
  toFixed,
  moneyFilter,
  QFormat,
  relationtoAppntFilter,
  formatPassTime,
  transformDate
}
